import React from 'react'
import { Link } from 'react-router-dom'

export default function Footer() {

    return (

        <div>
            <div className="footer">
                <div className="row">
                    <Link className="col5" to="/">

                        <img src="https://m.efshop.com.tw/img/btn_web_home.1f0b017e.png" alt="" />
                        <span>首頁</span>

                    </Link>


                    <Link className="col5" to="/classification">

                        <img src="data:image/png;base64," alt="" />
                        <span>分類</span>

                    </Link>


                    <Link className="col5" to="/member">

                        <img src="https://m.efshop.com.tw/img/btn_user.db8c95fd.png" alt="" />
                        <span>會員</span>

                    </Link>


                    <Link className="col5" to="/favorite">

                        <img src="https://m.efshop.com.tw/img/btn_favorite.cc47d37c.png" alt="" />
                        <span>收藏</span>

                    </Link>


                    <Link className="col5" to="/checkout">

                        <img src="https://m.efshop.com.tw/img/btn_shoping_cart.ff52235d.png" alt="" />
                        <span>結帳</span>

                    </Link>
                </div>

            </div>

        </div>
    );

}

